.wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 98;
    grid-template-areas:
    "news-room"
    "category-bar"
    "article-list"
    "footer"
}  

.nav-wrapper {


}
  
#article-list {
  grid-area: article-list;
  text-align: left;
  background: greenyellow;
  font-size: 20px;
  
  
  } 


.navbar {
  background: grey;
  color: black;
  /* position: fixed; */
  top: 0;
  width: 100%;
  z-index: 50;
}

 #category-bar {
  grid-area: category-bar;
  text-align: center;
  background: green;
  font-size: 24px;
 }

.news-room {
  grid-area: news-room;
  text-align: center;
  background-color: blueviolet;
  font-family: 'Libre Baskerville', serif;
  font-size: 62px;
  } 



